<template>

	<view class="paymentList">
		
		<view id="recharge">
			<view class="recharge-container">
				<view class="weui-cell weui-cell_select weui-cell_select-after weui-line">
					<view class="weui-cell__hd">
						<view for="" class="weui-view">缴费类型</view>
					</view>
					<view class="weui-cell__bd icon-select" id="">
						<picker @change="bindPickerChange" :value="index" :range="array">
							<view class="uni-input">{{array[index]}}</view>
						</picker>
					</view>
				</view>
				<view class="weui-cell weui-cell_select weui-cell_select-after weui-line">
					<view class="weui-cell__hd">
						<view for="" class="weui-view">缴费单位</view>
					</view>
					<view class="weui-cell__bd icon-select" id="">
						<picker @change="bindPickerChange2"  range-key="name" :value="key" :range="company">
							<view class="uni-input">{{company[key].name}}</view>
						</picker>
					</view>
				</view>
				<view v-if="type_ac" class="weui-cell weui-line">
					<view class="weui-cell__hd ">
						<view class="weui-view">Account</view>
					</view>
					<view class="weui-cell__bd ">
						<input type="number" :placeholder="acPlac" placeholder-class="plac" @input=" recordAc">
						</input>

						<i class="weui-icon weui-icon-info-circle"></i>
					</view>

				</view>
				<view v-if="type_ac" class="weui-cell weui-line">
					<view class="weui-cell__hd ">
						<view class="weui-view">Identifier</view>
					</view>
					<view class="weui-cell__bd ">
						<input type="number" :placeholder="idPlac" placeholder-class="plac" @input=" recordId">
						</input>

						<i class="weui-icon weui-icon-info-circle"></i>
					</view>

				</view>
				<view class="weui-cell weui-line">
					<view class="weui-cell__hd ">
						<view class="weui-view">缴费金额</view>
					</view>
					<view class="weui-cell__bd ">
						<input type="number" placeholder="请填写账单金额(¥)" placeholder-class="plac" @input=" recordMoney">
						</input>

						<i class="weui-icon weui-icon-info-circle"></i>
					</view>

				</view>
				<view class="weui-cell ">
					<view class="weui-cell__hd ">
						<view class="weui-view">手续费</view>
					</view>
					<view class="weui-cell__bd ">
						<view style="color: #d55b5b;">¥{{service_charge}}</view>

						<i class="weui-icon weui-icon-info-circle"></i>
					</view>

				</view>


			</view>
		</view>
	
	<view class="link-content">
	
				<navigator class="link-wrap historycard " url="../lifehistory/lifehistory">
					<button class=" confirm-light " type="theme-lihght">历史订单</button>
				</navigator>
			
				<view class="link-wrap historycard" >
					<button class=" confirm " type="theme"  @click="lifeorder">立即缴费</button>
				</view>
				
				
	
			</view>
			
			
			<!-- ？需要点击遮罩物管关闭的效果 -->
			<uni-popup ref="popupmore" type="more" :maskclick="false">
				<uni-popup-more title="popumore"></uni-popup-more>
			</uni-popup>
	</view>
</template>
<script>
		import uniPopup from '@/components/uni-popup/uni-popup.vue'
		import uniPopupMore from '@/components/uni-popup/uni-popup-more.vue'
	export default {
		name: "paymentlist",

		components: {
			uniPopup,
			uniPopupMore
			// uniPopupMore
		
		
		},
		data() {
			// const currentDate = this.getDate({
			//         format: true
			//     })

			return {
				index: 0,
				key: 0,
				apiUrl: 'https://ccc.wangwanglive.com/index.php/api/V1/',


				array: ['请选择缴费类型', '水费', '电费', '网费'],
				// s-静态数据
				company: [{
						"name": "请选择缴费类型"
					},

				],
				info: {

				},
				//e-静态数据
                payment:"",
				acPlac: "Account No",
				idPlac: "Identifier",
				service_charge:"50",
				type_ac: false,
				itemid:"",
				ac: "",
				ident: "",
				money: "",

			}
		},
			props:[
		"sf",
		"df",
		"wf"
		
	], 
		methods: {
				 recordAc:function(e){
						   this.ac = e.detail.value; 
						  
					 },
					 recordId:function(e){
						  this.ident = e.detail.value; 
					
					 },
					 recordMoney:function(e){
						  this.money = e.detail.value; 
						  	if(this.money>3000){
						      			this.service_charge=Math.floor(this.money*2*0.01);
						  
						      		}else{
						  
						      			this.service_charge=50;   	
						      		}
						          //conso
						  	   console.log(this.ac,this.ident,this.money);
					 },
            lifeorder:function(){
				var _this = this;
				
				
		    if(!_this.itemid) {
		  uni.showToast({
		       title:'请输入缴费单位',  
		       icon:"none"  
		         }); 
					return false;
		
				}
		
		
				if(!_this.ac||!_this.ident) {
		
				  uni.showToast({
						title:'请填写正确的账单信息',
						icon:"none"  
					   })
		
					return false;
		
				}
		
				if(!_this.money) {
				uni.showToast({
						title:'请填写缴费金额',
						icon:"none"  
							 })
		
					return false;
		
				}
				
						/*  #ifdef  MP-WEIXIN  */
								 _this.payment='wxpay_mini_app';
								/*  #endif  */
								
								/*  #ifdef  MP-ALIPAY  */
								 _this.payment='alipay_mini_app';
								   /*  #endif  */
				
				// uni.showToast({
				// 		title:'支付中...',
				// 		 icon:"none"  ,
				// 		 // mask:true,
				// 		duration:2000
				// 			 }) 
				
					this.$refs.popupmore.open()
					
						uni.request({
							url: "https://ccc.wangwanglive.com/index.php/index/lifepay/vlifecreate",
							data: {
									price: _this.money , 
									itemid: _this.itemid  ,
									service_price: _this.service_charge  ,
									Identifier: _this.ident  ,
									AccountNo: _this.ac  , 
									user_id: uni.getStorageSync('userlist').user_id,
									payid:uni.getStorageSync('lpayid'),
									payment: _this.payment,
							},
							// header:{
							// 	"Access-Control-Allow-Origin": "*"
							// },
						
							method: "POST",
							success: function(res) {
								_this.$emit('fun', false)
								if (res.data.code=='200') { 
							 uni.setStorageSync( 'lpayid', res.data.id );
						let data = JSON.parse(res.data.data);
							 console.log(data);
									    if(data.code==0 && data.desc=='success' ){
									
									        let pay_param = JSON.parse(data.data.content);
									        console.log(pay_param);
									        _this.juancashpay(pay_param);          
									      }else{ 
									
									uni.showToast({
									       title:''+data.desc+'',  
									       icon:"none"  
									         }); 
									
									     } 
							 
							 
							
								} else {
									uni.showToast({
									      title:''+data.msg+'',  
									       icon:"none"  
									         }); 
								}
						
						
						
						
							},
							fail: function(error) {
								console.log(error)
						
							}
						})
				
			console.log(_this.money)		
			console.log(_this.ac)	
			console.log(_this.ident)	
					
				
				
			},
			
						juanalipay(orderInfo){
							this.$refs.popupmore.close()
						 console.log(orderInfo); 
						 uni.requestPayment({ 
						provider: 'alipay',
						 orderStr: ''+orderInfo+'', // ==tradeNO 
						success: function (res) {
						 console.log('success:' + JSON.stringify(res)); 
						 	 if (res.errMsg === 'requestPayment:ok') {
						 uni.removeStorageSync('lpayid')
						 uni.showToast({
						        title:'支付成功',  
						        icon:"none"  
						          }); 
						 			 uni.redirectTo({
						 				 url:'/pages/lifepay/lifepay'
						 					    });
												}
						}, fail: function (err) { 
						console.log('fail:' + JSON.stringify(err)); 
						} 
						});	
							
							
						},
			
			
		juancashpay(item){
				this.$refs.popupmore.close()
			      uni.requestPayment({
			          provider: 'wxpay',
			          timeStamp: ''+item.timeStamp+'',//时间戳
			          nonceStr: ''+item.nonceStr+'',//随机字符串
			          package: ''+item.package+'',//统一下单接口返回的 prepay_id 参数值
			          signType: ''+item.signType+'',
			          paySign: ''+item.sign+'',//签名内容
			           success: function (res) {
						 if (res.errMsg === 'requestPayment:ok') {
						 　　　　　　　//　Toast('微信支付成功')
						 　　　　　　　　//that.$router.replace({name:'fullOrder',query:{id:'2'}})
						uni.removeStorageSync('lpayid')
				      uni.showToast({
				             title:'支付成功',  
				             icon:"none"  
				               }); 
				      			 uni.redirectTo({
				      				 url:'/pages/lifepay/lifepay'
				      					    });
				      			return; 
						 }
			              console.log('success:' + JSON.stringify(res));
			           },
			           fail: function (err) {
			              console.log('fail:' + JSON.stringify(err));
			           }
			       });
			 
			 },	
			 
			fxpay(item){
			this.$refs.popupmore.close()
		uni.requestPayment({
		    provider: 'wxpay',
		    timeStamp: ''+item.timeStamp+'',//时间戳
		    nonceStr: ''+item.nonceStr+'',//随机字符串
		    package: ''+item.package+'',//统一下单接口返回的 prepay_id 参数值
		    signType: ''+item.signType+'',
		    paySign: ''+item.paySign+'',//签名内容
		     success: function (res) {
				// console.log(res)
							 if (res.errMsg === 'requestPayment:ok') {
							 　　　　　　　//　Toast('微信支付成功')
							 　　　　　　　　//that.$router.replace({name:'fullOrder',query:{id:'2'}})
							uni.removeStorageSync('payid')
				   uni.showToast({  
				          title:'支付成功',  
				          icon:"none"  
				            }); 
						   uni.redirectTo({
						           url:'/pages/index/index'
						                    });
											return;
							 }
		        console.log('success:' + JSON.stringify(res));
		     },
		     fail: function (err) {
		        console.log('fail:' + JSON.stringify(err));
		     }
		 });
			  
			},	



			bindPickerChange: function(e) {
				var _this = this;
				// 重置company
				_this.company = [{
					"name": "请选择缴费类型"
				}];
				console.log('picker发送选择改变，携带值为', e.target.value)
				// 每次选择,需要将下一级清零
				_this.index = e.target.value
				_this.key = 0
	
             console.log(_this.index)
		
			/*  #ifdef  MP-WEIXIN  */
				switch( _this.index ){
						case '1':{
							_this.company = _this.company.concat(_this.sf)
							break;
						}
						case '2':{
							_this.company = _this.company.concat(_this.df)
							break;
						}
						case '3':{
						    _this.company = _this.company.concat(_this.wf)
							break;
						}
					}
					  /*  #endif  */
					  
					/*  #ifdef  MP-ALIPAY  */	
				switch( this.index ){
						case 1:
							_this.company = _this.company.concat(_this.sf)
							break;
							case 2:
								_this.company = _this.company.concat(_this.df)
								break;
								case 3:
									_this.company = _this.company.concat(_this.wf)
									break;
						}	
					  /*  #endif  */
					
					
					
					

				_this.type_ac = false;
			},
			bindPickerChange2: function(e) {

				//将数据统一在之前请求的数据中，将其全部包含在COMPANY中

				this.key = e.target.value
				if (this.key == 0 || this.index == 0) {
					this.type_ac = false
					return
				}

				// 选取公司之后,将placeholder传入ACCOUNT&ID中

				var obj = JSON.parse(this.company[this.key].param)
				this.company[this.key].id

               console.log(this.company[this.key])


                this.itemid=this.company[this.key].fid

				this.acPlac = obj[0].note
				this.idPlac = obj[1].note
				// this.idPlac = this.company[this.key].id



				// console.log(this.acPlac)

				this.type_ac = true;

			},

		},
	}
</script>
<style scoped>
	/* 	.weui-cell.weui-line:before {
		content: " ";
		position: absolute;
		left: 0;
		top: 0;
		right: 0;
		height: 1px;
		border-top: 1px solid #e5e5e5;
		color: #e5e5e5;
		-webkit-transform-origin: 0 0;
		transform-origin: 0 0;
		-webkit-transform: scaleY(.5);
		transform: scaleY(.5);
		left: 15px;
		z-index: 2;

	} */
	.plac {
		color: #d5d5d5;
		font-size: 14px;
	}

	.weui-cell.weui-line {
		border-bottom: 1px solid #ddd;
	}

	input {
		width: 100%;
		/* #ifdef MP-ALIPAY */
		font-size: .95em;
		line-height: 2.6em;
		height: 2.6em;
		padding: 0;

		/* #endif */
	}

	#recharge {

		font-size: 14px;
		color: #333;
	}

	.recharge-container {
		background-color: #FFF;
		border-radius: 15px;
	}

	.weui-cell {
		margin-left: 30rpx;
		margin-right: 30rpx;

		display: -webkit-box;
		display: -webkit-flex;
		display: flex;
		-webkit-box-align: center;
		-webkit-align-items: center;
		align-items: center;
		font-weight: 700;
		font-size: 1em;
		line-height: 2.5rem;
		height: 2.5rem;
		position: relative;
		/* #ifdef MP-ALIPAY */
		line-height: 2.6em;
		height: 2.6em;
		/* #endif */
	}

	.weui-cell__hd {
		width: 30%;
		text-align: left;
		font-weight: bold;
	}

	.weui-cell__bd {
		width: 70%;
		text-align: left;
		font-weight: 400;
		/* #ifdef MP-ALIPAY */
		line-height: 2.6em;
		height: 2.6em;
		/* #endif */
	}



	.picker.weui-select.icon-select {
		width: 100%;
	}

	.icon-select:after {
		content: " ";
		display: inline-block;
		height: 10rpx;
		width: 10rpx;
		border-width: 2px 2px 0 0;
		border-color: #C8C8CD;
		border-style: solid;
		-webkit-transform: matrix(0.71, .71, -.71, .71, 0, 0);
		transform: matrix(0.71, .71, -.71, .71, 0, 0);
		position: relative;
		top: -2px;
		position: absolute;
		top: 50%;
		right: .2rem;
		margin-top: -4px;
	}

	.weui-cell__bd.application-flex {
		text-align: left;
		box-sizing: border-box;
		padding-left: 15px;

	}

	.select-down {
		background-color: #e43;

	}

	.select-down .picker-dropdown__item {

		padding: 0 !important;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;


	}

	.btn-half {
		width: calc((100% - 15px)/2);
	}

	.well-line:before {
		/* 		content: " ";
		position: absolute;
		left: 0;
		top: 0;
		right: 0;
		height: 1px;
		border-top: 1px solid #e5e5e5;
		color: #e5e5e5;
		-webkit-transform-origin: 0 0;
		transform-origin: 0 0;
		-webkit-transform: scaleY(.5);
		transform: scaleY(.5);
		left: 15px;
		z-index: 2; */
		border-bottom: 1px solid #e5e5e5;
	}

	.btnArea {
		box-sizing: box-sizing;
		display: flex;
		flex-direction: row;
		align-items: center;
	}

	.mar-15 {
		margin-top: 20px;
		margin-left: 15px;
		margin-right: 15px;
	}

	.mar-r15 {
		margin-right: 15px;
	}

	.historycard {
		width: 100%;
	}

	.uni-input {
		word-break: break-all;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis
	}

	/* button */
	.button-hover[type=theme],.button-hover[type=theme-lihght] {
		color: hsla(0, 0%, 100%, .6);
		background-color: #942228
	}
	

	uni-button[type=theme] {
		color: #fff;
		background-color: var(--theme-color);
	}
	
	uni-button[type=theme-lihght]{
		color:#fff;
		background-color:var(--theme-color);
		/* #ifdef MP-ALIPAY */
		background-color: var(--theme-light);
		/* #endif */
	}
	.confirm-light{
	/* #ifdef MP-WEIXIN */
	color:#fff;
	background-color: var(--theme-color);
	/* #endif */
}




	.link-wrap {
		margin-top: 40rpx;
		display: flex;
		justify-content: center;
		align-items: center;

	}

	.link-wrap button {
		line-height: 39.2px!important;
		height: 39.2px!important;
		border-radius:5px;
		width: 100%;
		margin-left: 5px;
		margin-right: 5px;
	
	}

	.historycard {
		width: 50%;
	}

	.link-content {
		display: flex;
		align-items: center;
		justify-content: center;
	}
</style>
